<template>
	<div class="large-deposit-buy">
        <mt-cell @click.native="showSelectorPaymentAccount()" title="卡号" placeholder="" v-model="detail.account" is-link>
        </mt-cell>
		<selector-payment-account ref="selectorPaymentAccount" :options="selectorPaymentAccount" ></selector-payment-account>
        <mt-cell class="mint-select" title="产品状态：" is-link>
            <select class="mint-field-core" v-model="detail.status">
                <option value="">不限</option>
                <option value="1">即将发行</option>
                <option value="2">可购买</option>
                <option value="3">已发行</option>
            </select>
        </mt-cell>
        <mt-cell class="mint-select" title="产品期限：" is-link>
            <select class="mint-field-core" v-model="detail.date">
                <option value="">不限</option>
                <option value="1">1个月</option>
                <option value="3">3个月</option>
                <option value="6">6个月</option>
                <option value="9">9个月</option>
                <option value="12">12个月</option>
                <option value="18">18个月</option>
                <option value="24">24个月</option>
                <option value="36">36个月</option>
                <option value="60">60个月</option>
            </select>
        </mt-cell>
        <mt-cell class="mint-select" title="排序方式：" is-link>
            <select class="mint-field-core" v-model="detail.type">
                <option value="">存单利率</option>
                <option value="1">发行日期</option>
            </select>
        </mt-cell>
		<div class="field-action">
            <mt-button @click.native="back" type="primary">返回</mt-button>
            <submit-button @submit="submit" :submitStatus="submitStatus" value="确定"></submit-button>
		</div>
	</div>
</template>

<script>

import { MessageBox } from 'mint-ui-rem-dpr';
import service from '@/services/bank/finance-products';

export default{
	data() {
		return {
			detail: {
                account: '',
				status: '2',
				date: '',
                type: '1'
			},
            submitStatus: false,
            seletsItem: {
                days: [
                    {
                        val: '',
                        desc: '不限'
                    }
                ],
                keeps: [
                    {
                        val: '',
                        desc: '不限'
                    }
                ],
                profits: [
                    {
                        val: '',
                        desc: '不限'
                    }
                ],
                status: [
                	{
                        val: '',
                        desc: '不限'
                    }
                ]
            },
			selectorPaymentAccount: {
                title: '选择理财账户',
                checked: {},
                showAddBtn: false,
                showWarn: false,
                accountList: [
                    {
                        id: 1,
                        name: '郭振宇',
                        account: '625228***0089',
                        type: '储蓄卡',
                        isDefault: true,
                        idCardType: '身份证',
                        idCard: '550505****50505'
                    },
                    {
                        id: 2,
                        name: '郭振宇2',
                        account: '625228***0025',
                        type: '储蓄卡'
                    },
                    {
                        id: 3,
                        name: '郭振宇3',
                        alias: '',
                        account: '625228***0109',
                        type: '储蓄卡',
                        bankName: '安徽农金'
                    }
                ]
            }
		};
	},
    watch: {
        'detail.account': function(val) {
            if(val) this.submitStatus = true;
            else this.submitStatus = false;
        }
    },
	methods: {
		showSelectorPaymentAccount() {
            this.$refs.selectorPaymentAccount.show().then(data => {
                this.selectorPaymentAccount.checked = data.item;
                this.detail.account = data.item.account;
            }, data => {
            });
        },
        submit() {
        	this.$router.push({name: 'LargeDepositBuyList'});
        },
        back() {
        	history.back();
        }
    },
	created() {
		this.detail = Object.assign({}, this.$store.state.largeDepositBuy.detail);
	},
	beforeRouteLeave (to, from, next) {
        if(to.meta['groupName'] === from.meta['groupName']){
            this.$store.dispatch('updateLargeDepositBuy', {key: 'detail', value: this.detail});
        }else{
            this.$store.dispatch('updateLargeDepositBuy', {key: 'detail', value: {account: '', status: '', date: '', type: ''}});
        }
        next( true );
    }
};

</script>

<style lang="scss">
.large-deposit-buy{
	.mint-cell-title,{
		width: 3.6rem;
	}
    .mint-cell.mint-select{
        .mint-cell-title{
            width: 3.6rem;
        }
    }
	.colorRed{
		.mint-field-core{
			color: red;
		}
	}
}
</style>


